
'use server';

import { cookies } from 'next/headers';
import { redirect } from 'next/navigation';
import { z } from 'zod';
import {
  createEvent as apiCreateEvent,
  joinEvent as apiJoinEvent,
  leaveEvent as apiLeaveEvent,
  checkInUser as apiCheckInUser,
  // ADMIN_CREATE_EVENT_PASSWORD is no longer used here for validation
  getUserById,
  getUserByUsername,
  getEventById,
  getEvents, 
  createUser as apiCreateUser,
  getEventByCdKey,
  getAnnouncements as apiGetAnnouncements,
} from './data';
import type { Event, User, Announcement } from './types';
import { checkNameMC } from './namemc';

const MAX_AGE = 60 * 60 * 24 * 30; 

const LoginSchema = z.object({
  minecraftUsername: z.string().min(1, "Minecraft 用户名不能为空。"),
  password: z.string().min(1, "密码不能为空。"),
});

export async function login(prevState: any, formData: FormData) {
  try {
    const validatedFields = LoginSchema.safeParse(Object.fromEntries(formData.entries()));

    if (!validatedFields.success) {
      return {
        message: '输入无效。请检查您的用户名和密码。',
        errors: validatedFields.error.flatten().fieldErrors,
      };
    }

    const { minecraftUsername, password } = validatedFields.data;
    let user = await getUserByUsername(minecraftUsername);

    if (user) {
      // User exists, check password
      if (user.password !== password) { // Plaintext comparison - FOR PROTOTYPE ONLY
        return { message: '密码不正确。', errors: { password: ['密码不正确。'] } };
      }
    } else {
      // User does not exist, attempt to register
      const isNameMCValid = await checkNameMC(minecraftUsername);
      if (!isNameMCValid) {
        return { message: '此 Minecraft 用户名在 NameMC 上无效或无法验证。', errors: { minecraftUsername: ['此 Minecraft 用户名在 NameMC 上无效或无法验证。'] } };
      }
      try {
        // For new users, their ID will be their username, op will be 0 (default)
        user = await apiCreateUser({ 
          id: minecraftUsername, // Use username as ID for new users
          username: minecraftUsername,
          password: password, // Save the provided password
          // avatarUrl, joinedEventIds, op will be set to defaults by apiCreateUser
        });
      } catch (error: any) {
        console.error("登录操作中的用户创建失败 (apiCreateUser):", error);
        return { message: `创建用户时出错: ${error.message || '未知错误'}`, errors: {} };
      }
    }

    if (user && user.id) {
      // console.log(`Login successful for user: ${user.username}, ID: ${user.id}. Redirecting to dashboard.`);
      cookies().set('userId', user.id, {
        httpOnly: true,
        secure: process.env.NODE_ENV === 'production',
        maxAge: MAX_AGE,
        path: '/',
        sameSite: 'lax',
      });
      redirect('/dashboard');
    } else {
      // This case should ideally not be reached if user creation or lookup is successful
      return { message: '登录或注册失败，无法找到或创建用户账户。', errors: {} };
    }
  } catch (error: any) {
     if (error.digest?.startsWith('NEXT_REDIRECT')) {
      throw error; // Re-throw Next.js redirect errors
    }
    // Log any other unexpected errors
    console.error("登录服务器操作发生内部错误:", error);
    return { message: '登录服务暂时遇到问题，请稍后再试。', errors: {} };
  }
}


export async function logout() {
  cookies().delete('userId');
  redirect('/login');
}

export async function getSessionUser(): Promise<User | null> {
  const userId = cookies().get('userId')?.value;
  if (!userId) return null;
  try {
    const user = await getUserById(userId);
    return user || null;
  } catch (error) {
    console.error("获取会话用户失败:", error);
    return null;
  }
}

const CreateEventSchema = z.object({
  name: z.string().min(3, "活动名称至少需要3个字符。"),
  date: z.string().refine((date) => !isNaN(Date.parse(date)), "无效的日期格式。"),
  description: z.string().min(10, "描述至少需要10个字符。"),
  participantLimit: z.preprocess(
    (val) => (val === "" || val === null || val === undefined ? undefined : Number(val)),
    z.number().int().positive().optional()
  ),
  cdKey: z.string().min(4, "活动代码至少需要4个字符。").max(20, "活动代码最多20个字符。"),
  imageUrl: z.string().url("无效的图片链接。").optional().or(z.literal('')),
  userPassword: z.string().min(1, "您的账户密码不能为空。") // Changed from adminPassword
});

export async function createEventAction(prevState: any, formData: FormData) {
  const rawFormData = Object.fromEntries(formData.entries());
  const validatedFields = CreateEventSchema.safeParse(rawFormData);

  if (!validatedFields.success) {
    return {
      errors: validatedFields.error.flatten().fieldErrors,
      message: '创建活动失败。请检查所填字段。',
    };
  }

  const user = await getSessionUser();
  if (!user) {
    return { message: '需要认证才能创建活动。请先登录。', errors: {} };
  }
  if (user.op !== 1) {
    return { message: '您没有创建活动的权限。', errors: {} };
  }

  // Verify user's own password
  if (!user.password || user.password !== validatedFields.data.userPassword) {
    return {
      errors: { ...validatedFields.error?.flatten().fieldErrors, userPassword: ['您的账户密码不正确。'] },
      message: '创建活动失败。账户密码验证失败。',
    };
  }

  let newEventId: string | undefined;
  try {
    // Exclude userPassword from the data passed to apiCreateEvent
    const { userPassword, ...eventDataForApi } = validatedFields.data;
    const newEvent = await apiCreateEvent(
        eventDataForApi as Omit<Event, 'id' | 'participants' | 'organizerId'>, // Type assertion might be needed
        user.id
    );
    newEventId = newEvent.id;
  } catch (error) {
    console.error("创建活动错误:", error);
    return { message: '在服务器上创建活动失败。请稍后重试。', errors: {} };
  }

  if (newEventId) {
    redirect(`/events/${newEventId}`);
  } else {
    return { message: '创建活动后未能获取活动ID。', errors: {} };
  }
}

export async function joinEventAction(eventId: string, cdKey: string) {
  const user = await getSessionUser();
  if (!user) {
    return { success: false, message: '您必须登录才能加入活动。' };
  }
  try {
    const result = await apiJoinEvent(user.id, eventId, cdKey);
    return result;
  } catch (error) {
    console.error("加入活动错误:", error);
    return { success: false, message: '加入活动时发生服务器错误。' };
  }
}

export async function joinEventUsingCdKeyOnlyAction(cdKey: string): Promise<{ success: boolean, message: string, event?: Event }> {
  const user = await getSessionUser();
  if (!user) {
    return { success: false, message: '您必须登录才能加入活动。' };
  }

  if (!cdKey || cdKey.trim() === '') {
    return { success: false, message: '请输入有效的活动代码。' };
  }

  const eventToJoin = await getEventByCdKey(cdKey);
  if (!eventToJoin) {
    return { success: false, message: '具有此活动代码的活动未找到。' };
  }

  try {
    // Call the existing joinEventAction, which handles all joining logic including CD-Key check
    const result = await apiJoinEvent(user.id, eventToJoin.id, cdKey); 
    return result;
  } catch (error) {
    console.error("使用活动代码加入活动错误:", error);
    return { success: false, message: '使用活动代码加入活动时发生服务器错误。' };
  }
}


