
import LoadingLink from '@/components/loading-link';
import { Gem, LogIn, UserCircle, PlusCircle, LogOut as LogOutIcon, LayoutDashboard } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { getSessionUser } from '@/lib/actions';
import { Badge } from '@/components/ui/badge';


export default async function Header() {
  const user = await getSessionUser();

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-16 items-center justify-between">
        <div className="flex items-center">
          <LoadingLink href="/" className="mr-6 flex items-center space-x-2">
            <Gem className="h-6 w-6 text-primary" />
            <span className="font-bold sm:inline-block text-lg">
              MineVent
            </span>
            <Badge variant="outline" className="ml-2 border-orange-500 text-orange-600 dark:border-orange-400 dark:text-orange-400 text-xs px-1.5 py-0.5">Beta</Badge>
          </LoadingLink>
          <nav className="flex items-center space-x-4 lg:space-x-6">
            <Button variant="ghost" asChild>
              <LoadingLink href="/">活动</LoadingLink>
            </Button>
            {user && (
               <Button variant="ghost" asChild>
                  <LoadingLink href="/dashboard">仪表盘</LoadingLink>
              </Button>
            )}
          </nav>
        </div>
        
        <div className="flex items-center space-x-4">
          {user ? (
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" className="relative h-8 w-8 rounded-full">
                  <Avatar className="h-8 w-8">
                    <AvatarImage src={user.avatarUrl || `https://avatar.vercel.sh/${user.username}.png?text=${user.username.substring(0,1)}`} alt={user.username} data-ai-hint="minecraft avatar" />
                    <AvatarFallback>{user.username.substring(0, 2).toUpperCase()}</AvatarFallback>
                  </Avatar>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent className="w-56" align="end" forceMount>
                <DropdownMenuLabel className="font-normal">
                  <div className="flex flex-col space-y-1">
                    <p className="text-sm font-medium leading-none">{user.username}</p>
                    <p className="text-xs leading-none text-muted-foreground">
                      {user.id} 
                    </p>
                  </div>
                </DropdownMenuLabel>
                <DropdownMenuSeparator />
                <DropdownMenuItem asChild>
                  <LoadingLink href="/dashboard">
                    <LayoutDashboard className="mr-2 h-4 w-4" />
                    <span>我的仪表盘</span>
                  </LoadingLink>
                </DropdownMenuItem>
                {user.op === 1 && (
                  <DropdownMenuItem asChild>
                    <LoadingLink href="/admin/create-event">
                      <PlusCircle className="mr-2 h-4 w-4" />
                      <span>创建活动</span>
                    </LoadingLink>
                  </DropdownMenuItem>
                )}
                <DropdownMenuSeparator />
                <DropdownMenuItem asChild>
                  <form action="/api/auth/logout" method="POST" className="w-full">
                     <button type="submit" className="w-full text-left flex items-center p-2 hover:bg-accent rounded-sm text-sm relative cursor-default select-none items-center gap-2 outline-none transition-colors focus:bg-accent focus:text-accent-foreground data-[disabled]:pointer-events-none data-[disabled]:opacity-50">
                        <LogOutIcon className="mr-2 h-4 w-4" />
                        <span>退出登录</span>
                      </button>
                  </form>
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          ) : (
            <Button asChild>
              <LoadingLink href="/login">
                <LogIn className="mr-2 h-4 w-4" /> 登录
              </LoadingLink>
            </Button>
          )}
        </div>
      </div>
    </header>
  );
}
